package com.jonnyzzz.logo.script


fun parseExpressionList(source: ExpressionTokenSource, minElements: Int? = null, maxElements: Int? = null) : List<IntExpression> {
  val result = parseOpList(
    source = source,
    parseExpression = ::parseExpression,
    parseOperator = {
      when (it) {
        ',' -> ExpressionListOp.ACCEPT
        else -> ExpressionListOp.FAIL
      }
    },
    acc = { listOf(it) },
    reduce = { result, _, it ->
      if (maxElements != null && result.size >= maxElements) {
        source.parseError("Too many elements in the list")
      }

      result + it
    }
  )

  if (minElements != null && result.size < minElements) {
    source.parseError("Too few elements in the list")
  }

  return result
}
