package com.jonnyzzz.logo.web.css

import kotlinx.css.*
import kotlinx.css.properties.border

object JogoStyle {

  val jogoBorderColor get() = Color.brown

  fun mainBorder(builder: CssBuilder) = builder.apply {
    border(2.px, BorderStyle.solid, jogoBorderColor, 5.px)
  }

  fun html(builder: CssBuilder) = builder.apply {

    children("div") {
      backgroundColor = Color.transparent
    }

    children("a, a:hover, a:visited, a:link") {
      color = Color.black
      fontStyle = FontStyle.normal
    }

    textarea {
      backgroundColor = Color.transparent
    }
  }

  fun jetbrainsMono(builder: CssBuilder) = builder.apply {
    fontFamily = "'JetBrains Mono', monospace"
  }
}


