package com.jonnyzzz.logo.web.i18

import com.jonnyzzz.logo.web.reducers.useJogoSelector
import kotlinx.serialization.Serializable
import react.Props
import react.RBuilder
import react.fc

object Messages {
  object Titles {
    const val jogo = "Jogo"
  }

  val docTableCommandTitle = Message(
    en = "Command",
    ru = "Команда",
    de = "Command",
  )

  val docTableCommandDescription = Message (
    en = "Description",
    ru = "Описание",
    de = "Erklärung"
  )
}

@Serializable
enum class JogoLocalization {
  EN,
  RU,
  DE,
  ;

  fun extract(m: Message) = when (this) {
    DE -> m.de
    RU -> m.ru
    EN -> m.en
  }

  val presentableName
    get() = when (this) {
      EN -> Message(en = "English", ru = "English", de = "English")
      RU -> Message(en = "Русский", ru = "Русский", de = "Русский")
      DE -> Message(en = "Deutsch", ru = "Deutsch", de = "Deutsch")
    }
}

class Message(
  val en: String,
  val ru: String = en,
  val de: String = en
) {
  override fun toString() = en
}

fun RBuilder.msg(m: Message) {
  child(MessageComponent) { attrs.message = m }
}

fun RBuilder.msg(
  en: String,
  ru: String = en,
  de: String = en
) = msg(Message(en, ru, de))

private external interface MessageComponentProps : Props {
  var message: Message
}

private val MessageComponent = fc<MessageComponentProps> { props ->
  val state = useJogoSelector { locale }
  val message = state.extract(props.message)
  if (message.isNotBlank()) {
    +" "
    +message
    +" "
  }
}


