package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.i18.Message
import react.RBuilder

fun RBuilder.commands() {
  commandDocumentation {
    command(
      command = "home",
      explanation = Message(
        en = "Move Jogo to the center of the screen"
      )
    )

    command(
      command = "head <N>",
      explanation = Message(
        en = "Turns Jogo to the absolute angle, use 0 to turn it up"
      )
    ) {
      tSize(20)
      +"head 45"
    }

    command(
      command = "fd <N>",
      explanation = Message(
        en = "Move Jogo forward to 40 steps. It goes towards head direction"
      )
    ) {
      startPos(0, 40)
      fd(40)
    }

    command(
      command = "pu\npd",
      explanation = Message(
        en = "Turn off/on lines drawing when Jogo moves"
      )
    ) {
      startPos(0, 45)
      fd(30)
      pu()
      fd(30)
      pd()
      fd(30)
    }

    command(
      command = "lt <N>",
      explanation = Message(
        en = "Turns Jogo to the left on 30 degrees"
      )
    ) {
      tSize(20)
      lt(30)
    }

    command(
      command = "rt <N>",
      explanation = Message(
        en = "Turns Jogo to the right on 30 degrees"
      )
    ) {
      + "rt 30"
    }

    command(
      command = "arc <N>, <N>",
      explanation = Message(
        en = "Make Jogo to draw a circle with the center in the current point"
      )
    ) {
      arc(29, 222)
    }

    command(
      command = "circle <N>",
      explanation = Message(
        en = "Draws a circle with the center at the current location"
      )
    ) {
      circle(29)
    }

    command(
      command = "color <COLOR>",
      explanation = Message(
        en = "Changes the draw color, e.g red, green, blue, black"
      )
    ) {
      color("red")
      circle(29)
    }

    command(
      command = "color rgb <N>, <N>, <N>",
      explanation = Message(
        en = "Changes the draw color to RGB. Each color is from 0 to 255"
      )
    ) {
      colorRgb(122, 144, 55)
      circle(29)
    }

    command(
      command = "repeat <N>\n  <CMD>\nend",
      explanation = Message(
        en = "Repeats the command block N times"
      )
    ) {
      repeat(3) {
        startPos(35, 0)
        lt(45)
        fd(25)
      }
    }

    command(
      command = "push pos\npop pos",
      explanation = Message(
        en = "Remembers the current position and head angle. Last position is restored first"
      )
    ) {
      pushPos()
      fd(20)
      popPos()
    }

    command(
      command = "hide\nshow",
      explanation = Message(
        en = "Hides or shows the Jogo head"
      )
    ) {
      hide()
      frame(300)
      show()
      frame(300)
      replay()
    }

    command(
      command = "set <V> = <N>",
      explanation = Message(
        en = "Sets a variable to a value",
        de = "Legt den Wert einer Variablen fest",
        ru = "Устанавливает значение меременной"
      )
    ) {
      set("x", "1")
      repeat(6) {
        set("x", "x * 2")
        lt(30)
        fd("x")
        frame(300)
      }
      replay()
    }

    command(
      command = "frame <N>",
      explanation = Message(
        en = "Shows the current image and waits <N> milliseconds before next"
      )
    ) {
      frame(300)
      fd(10)
      frame(300)
      replay()
    }

    command(
      command = "replay",
      explanation = Message(
        en = "Restarts everything from the beginning"
      )
    ) {
      repeat(6) {
        lt(45)
        fd(12)
        frame(100)
      }
      replay()
    }

    command(
      command = "on key <KEY>\n  <CMD>\nend",
      explanation = Message(
        en = "Adds keys handler. KEY can be: up, down, left, right, space"
      )
    ) {
      onKey("left") {
        lt(45)
      }
      onKey("space") {
        fd(5)
      }
    }
  }
}
