package com.jonnyzzz.logo.script

sealed class JSvgElement {
  abstract val penColor: PenColor
}

data class JSvgLineElement(
  val from: ScriptPoint,
  val to: ScriptPoint,
  override val penColor: PenColor,
) : JSvgElement()

data class JSvgCircleElement(
  val center: ScriptPoint,
  val radius: Int,
  override val penColor: PenColor,
) : JSvgElement()

data class JSvgPathElement(
  val d : String,
  override val penColor: PenColor,
) : JSvgElement()

data class WorldSnapshot(
  val world: ScriptWorld,

  val startState: ScriptState,
  val finalState: ScriptState,

  val svg: List<JSvgElement>,
)

