package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.web.components.SvgIcon
import com.jonnyzzz.logo.web.components.svgIcon
import com.jonnyzzz.logo.web.i18.JogoLocalization
import com.jonnyzzz.logo.web.i18.msg
import com.jonnyzzz.logo.web.reducers.*
import kotlinx.css.*
import kotlinx.html.js.onClickFunction
import react.*
import react.dom.a
import react.dom.span
import react.dom.sup
import styled.styledDiv

private fun CssBuilder.mainContentCss() {
  display = Display.flex
  flexDirection = FlexDirection.row
  paddingBottom = 5.px

  child("a.menu-link") {
    padding(10.px)
    hover {
      backgroundColor = Color.brown.lighten(70)
    }
  }

  child("a.menu-back, a.menu-lang-selected") {
    fontWeight = FontWeight.bold
  }

  child(".menu-gap") {
    marginLeft = LinearDimension.auto
  }
}

val MainMenuComponent = fc<Props>(
  displayName = "main-menu",
) {
  val history = useJogoHistory()

  styledDiv {
    css.mainContentCss()

    if (history.isHelpPage()) {
      child(historyBackLink)
    } else if (!history.isStudio()) {
      child(historyBackToStudioLink)
    }

    if (history.isStudio()) {
      child(clearLink)
      child(resetLink)
      child(mazeLink)
      child(shareLink)
    }

    if (!history.isHelpPage() && !history.isSharePage()) {
      child(HelpLinkComponent)
    }

    span(classes = "menu-gap") {
      +" "
    }

    JogoLocalization.values().forEach { lang ->
      child(localeLink) {
        attrs.targetLocale = lang
        attrs.key = lang.name
      }
    }
  }
}

private val clearLink = fc<Props> {
  val dispatch = useJogoDispatch<StateAction>()

  a(href = "#", classes = "menu-link") {
    attrs.onClickFunction = {
      it.preventDefault()
      dispatch(ScriptClearAllAction())
    }

    msg(
      en = "Clean",
      de = "Löschen",
      ru = "Удалить"
    )

    sup {
      svgIcon(SvgIcon.TRASH, 16)
    }
  }
}

private val shareLink = fc<Props> {
  val history = useJogoHistory()
  a(href = "#", classes = "menu-link") {
    attrs.onClickFunction = history::pushShare
    +"Share"
  }
}

private val mazeLink = fc<Props> {
  val dispatch = useJogoDispatch<StateAction>()
  a(href = "#", classes = "menu-link") {
    attrs.onClickFunction = {
      it.preventDefault()
      dispatch(GenerateNewMazeAction())
    }

    msg(
      en = "Add Maze",
      ru = "Лабиринт",
    )
  }
}

private val resetLink = fc<Props> {
  val dispatch = useJogoDispatch<StateAction>()

  a(href = "#", classes = "menu-link") {
    attrs.onClickFunction = {
      it.preventDefault()
      dispatch(ScriptResetAllAction())
    }

    msg(
      en = "Load an example",
      de = "Beispiel laden",
      ru = "Загрузить пример"
    )
  }
}

private val HelpLinkComponent = fc<Props> {
  val history = useJogoHistory()

  a(href = "#/help", classes = "menu-link") {
    attrs.onClickFunction = {
      it.preventDefault()
      history.pushHelp()
    }

    msg(
      en = "Help",
      de = "Hilfe",
      ru = "Помощь"
    )

    sup {
      svgIcon(SvgIcon.HELP, 16)
    }
  }
}

private fun RBuilder.backMsg() = msg(
  en = "Back",
  de = "Zurück",
  ru = "Назад",
)


private val historyBackLink = fc<Props> {
  val routerHistory = useJogoHistory()

  a(href = "#", classes = "menu-link menu-back") {
    attrs.onClickFunction = {
      it.preventDefault()
      routerHistory.pushBack()
    }

    +"<< "
    backMsg()
  }
}

private val historyBackToStudioLink = fc<Props> {
  val routerHistory = useJogoHistory()

  a(href = "#", classes = "menu-link menu-back") {
    attrs.onClickFunction = {
      it.preventDefault()
      routerHistory.pushStudio(it)
    }

    +"<< "
    backMsg()
  }
}

private external interface LocalActionParameters : Props {
  var targetLocale: JogoLocalization
}

private val localeLink = fc<LocalActionParameters> { props ->
  val currentLocale = useJogoSelector { locale }
  val changeLocale = useJogoDispatch<ChangeLocaleAction>()
  val active = if (currentLocale == props.targetLocale) "menu-lang-selected" else ""
  a(href = "#", classes = "menu-link menu-lang $active") {
    attrs.onClickFunction = {
      it.preventDefault()
      changeLocale(ChangeLocaleAction(props.targetLocale))
    }

    msg(props.targetLocale.presentableName)
  }
}
