package com.jonnyzzz.logo.script

import kotlinx.coroutines.*
import kotlinx.coroutines.flow.MutableSharedFlow
import kotlinx.coroutines.flow.SharedFlow
import kotlinx.coroutines.flow.asSharedFlow
import kotlin.coroutines.CoroutineContext

class ReactiveWorldPlayer(
  private val world: ScriptWorld,
  private val initialState: ScriptState = ScriptState.ZERO,
  private val commands: ScriptMachine,
) {
  private val internalChangesFlow = MutableSharedFlow<WorldSnapshot>()
  val changes : SharedFlow<WorldSnapshot> = internalChangesFlow.asSharedFlow()

  fun CoroutineScope.start() {
    launch(CoroutineName("World Player")) {
      //make sure we are up-to-date
      yield()

      val scenes = WorldPlayer(world, initialState).play(commands)

      suspend fun playMe() {
        yield()
        for ((scene, action) in scenes) {
          yield()
          internalChangesFlow.emit(scene)
          yield()
          if (action == null) continue

          @Suppress("UNUSED_VARIABLE")
          val ignore = when (action) {
            is ScriptFrameDelayCommand -> delay(action.delayMillis.evaluate(initialState).coerceAtLeast(100).toLong())
            is ScriptReplayCommand -> {
              delay(300L)
              playMe()
              return
            }
          }
        }
      }

      playMe()
    }
  }
}
