package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.web.components.ScriptEditor3
import com.jonnyzzz.logo.web.components.SvgWorldMainComponent
import kotlinx.css.*
import react.Props
import react.dom.div
import react.fc
import styled.styledDiv

private fun CssBuilder.mainContentCss() {
  minWidth = 500.px
  display = Display.flex
  flexWrap = FlexWrap.wrap
  flexDirection = FlexDirection.row
  gap = 25.px

  fun defaultSize() = min(90.vw, 79.vh)

  child("div.main-script") {
    minWidth = 250.px
    maxWidth = 95.vw - defaultSize()
    paddingRight = 10.px
    maxHeight = defaultSize()
  }

  child("div.main-svg") {
    flex(3.0)
    minWidth = 300.px
    minHeight = 300.px

    maxWidth = defaultSize()
    maxHeight = defaultSize()
  }
}

val JogoStudio = fc<Props> {
  styledDiv {
    css.mainContentCss()

    div(classes = "main-svg") {
      child(SvgWorldMainComponent)
    }

    div(classes = "main-script") {
      child(ScriptEditor3)
    }
  }
}
