package com.jonnyzzz.logo.web.reducers

import com.jonnyzzz.logo.maze.MazePointNet
import com.jonnyzzz.logo.script.ScriptWorld
import com.jonnyzzz.logo.script.generateCode
import com.jonnyzzz.logo.web.CodeMirrorState
import com.jonnyzzz.logo.web.i18.JogoLocalization
import kotlinx.browser.window
import kotlinx.serialization.Serializable
import kotlinx.serialization.Transient
import kotlin.math.sqrt

@Serializable
data class JogoState(
  val locale: JogoLocalization = resolveDefaultLanguage(),
  val world: ScriptWorld = ScriptWorld(300, 300),
  val script: String = defaultScriptExample,

  @Transient
  val scriptEditorState: CodeMirrorState? = null,

  @Transient
  val maze: MazePointNet? = null,
)

private val defaultScriptExample get() = generateCode {
  fd(40)
  lt(90)
  fd(40)
  lt(90 + 45)
  fd(sqrt(0.0 + 40 * 40 + 40 * 40).toInt())
  pu()
  fd(90)
  pd()
}.text

private fun resolveDefaultLanguage(): JogoLocalization {
  runCatching {
    window.navigator.languages.forEach { lang ->
      for (value in JogoLocalization.values()) {
        if (lang.split("_", "-").first().startsWith(value.name, ignoreCase = true)) {
          return value
        }
      }
    }
  }
  runCatching {
    window.navigator.language.let { lang ->
      for (value in JogoLocalization.values()) {
        if (lang.split("_", "-").first().startsWith(value.name, ignoreCase = true)) {
          return value
        }
      }
    }
  }

  return JogoLocalization.EN
}
