package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.web.components.*
import com.jonnyzzz.logo.web.css.JogoStyle
import com.jonnyzzz.logo.web.i18.Message
import com.jonnyzzz.logo.web.i18.msg
import kotlinx.css.*
import kotlinx.css.properties.borderTop
import react.Props
import react.RBuilder
import react.dom.h2
import react.dom.p
import react.fc
import styled.styledDiv

private fun CssBuilder.mainContentCss() {
  width = min(80.pct, 800.px)

  "table.documentation-table" {
    child("thead") {
      fontWeight = FontWeight.bold
    }

    child("tbody") {
      child("tr") {
        paddingTop = 5.px
        marginBottom = 5.px
        borderTop(1.px, BorderStyle.solid, Color.lightSlateGray)
      }

      child("tr") {
        child("td.exp") {
          paddingTop = 10.px
          paddingBottom = 10.px
        }

        child("td.code") {
          paddingTop = 10.px
          paddingBottom = 5.px
          paddingRight = 50.px
          verticalAlign = VerticalAlign.middle

          child("pre") {
            JogoStyle.jetbrainsMono(this)
            fontWeight = FontWeight.bold
          }
        }
      }
    }
  }

  "td div.example" {
    paddingTop = 1.5.rem
  }

  "div.example" {
    display = Display.flex
    flexWrap = FlexWrap.wrap
    flexDirection = FlexDirection.row
    alignItems = Align.center
    gap = 25.px

    child(".svg") {
      width = 150.px
      height = 150.px
    }

    child(".code") {
      width = 250.px
      height = 150.px
    }

    textarea {
      resize = Resize.none
    }
  }
}

val JogoHelpHeader = Message(
  en = "Help",
  ru = "Помощь",
  de = "Hilfe",
)

val JogoHelp = fc<Props> {
  styledDiv {
    css.mainContentCss()

    p {
      msg(
        en = "Hello. Here you can control a small robot, it's name is Jogo. Here it is",
        ru = "Привет. Здесь ты можешь управлять небольшим роботом, которого зовут Jogo. Вот он",
      )
      svgIcon(SvgIcon.JOGO, size = 32, horizontalPadding = 5)
      +"."

      msg(
        en = "It can move, turn, and draw! His home is the screen",
        ru = "Он может двигаться, поворачиваться и рисовать! А живет он на экране"
      )

      msg(
        en = "You control the robot with text command, each on a dedicated line.",
        ru = "Ты можешь управлять роботом через команды, по одной на строку."
      )
    }

    p {
      msg(
        en = "For example you may draw the polyhedron with the following code:",
        ru = "Вот например многоугольник можно сделать при помощи такой программы:"
      )
    }

    svgExample {
      startPos(35, 15)
      repeat(8) {
        fd(30)
        lt(45)
      }
    }

    p {
      msg(
        en = "Here comes the list of commands that Jogo understands ",
        ru = "Ты можешь управлять роботом через команды, по одно на строку."
      )
    }

    commands()


    legend()
  }
}

private fun RBuilder.legend() {
  h2 {
    msg(
      en = "Legend",
      de = "Beschriftung",
      ru = "Сокращения"
    )
  }

  legendDocumentation {
    shortEntry(
      "<N>",
      "100",
      Message(
        en = "means a number or a formula with variables",
        de = "bedauted eine Nummmer or Formel mit Variabeln",
        ru = "число или формула с переменными",
      ),
    )

    shortEntry(
      "<V>",
      "x",
      Message(
        en = "variable name",
        de = "Name von Variabel",
        ru = "имя меременной",
      ),
    )

    shortEntry(
      "<CMD>",
      "lt 5\nfd 40",
      Message(
        en = "any program",
        ru = "другоая программа",
        de = "Anderes Programm",
      ),
    )
  }

}
