package com.jonnyzzz.logo.script


sealed interface IntExpression {
  fun evaluate(state: ScriptState): Int
}

data class VariableIntExpression(val name: String) : IntExpression {
  override fun evaluate(state: ScriptState): Int = state.intVariables[name] ?: 0
}

data class ConstIntExpression(val value: Int) : IntExpression {
  override fun evaluate(state: ScriptState): Int = value
}

data class NegateIntExpression(val node: IntExpression) : IntExpression {
  override fun evaluate(state: ScriptState): Int = - node.evaluate(state)
}

data class PlusIntExpression(val left: IntExpression, val right: IntExpression) : IntExpression {
  override fun evaluate(state: ScriptState): Int = left.evaluate(state) + right.evaluate(state)
}

data class MinusIntExpression(val left: IntExpression, val right: IntExpression) : IntExpression {
  override fun evaluate(state: ScriptState): Int = left.evaluate(state) - right.evaluate(state)
}

data class MultiplyIntExpression(val left: IntExpression, val right: IntExpression) : IntExpression {
  override fun evaluate(state: ScriptState): Int = left.evaluate(state) * right.evaluate(state)
}

data class DivideIntExpression(val left: IntExpression, val right: IntExpression) : IntExpression {
  override fun evaluate(state: ScriptState): Int {
    val right = right.evaluate(state)
    if (right == 0) return -0 //we need that to avoid errors while evaluation
    return left.evaluate(state) / right
  }
}

fun parseExpression(source: ExpressionTokenSource) : IntExpression {
  return parsePlusMinusValue(source)
}

private fun parsePlusMinusValue(source: ExpressionTokenSource): IntExpression {
  return parseOpList(
    source = source,
    parseExpression = ::parseMulDivValue,
    parseOperator = {
      when (it) {
        '-', '+' -> ExpressionListOp.ACCEPT
        else -> ExpressionListOp.STOP
      }
    },
    acc = { it },
    reduce = { acc, op, next ->
      when (op) {
        '+' -> PlusIntExpression(acc, next)
        '-' -> MinusIntExpression(acc, next)
        else -> error("Unexpected op: $op")
      }
    }
  )
}

private fun parseMulDivValue(source: ExpressionTokenSource): IntExpression {
  return parseOpList(
    source = source,
    parseExpression = ::parseUnaryValue,
    parseOperator = {
      when (it) {
        '*', '/' -> ExpressionListOp.ACCEPT
        else -> ExpressionListOp.STOP
      }
    },
    acc = { it },
    reduce = { acc, op, next ->
      when (op) {
        '*' -> MultiplyIntExpression(acc, next)
        '/' -> DivideIntExpression(acc, next)
        else -> error("Unexpected op: $op")
      }
    }
  )
}

private fun parseUnaryValue(source: ExpressionTokenSource): IntExpression {
  source.skipWhitespaces()

  val current = source.current

  if (current == '(') {
    source.next()
    val op = parseExpression(source)
    source.skipWhitespaces()
    if (source.current != ')') {
      source.parseError("Closing parenthesis is expected")
    }
    source.next()
    source.skipWhitespaces()
    return op
  }

  if (current == '-') {
    source.next()
    return NegateIntExpression(parseUnaryValue(source))
  }

  if (current?.isDigit() == true) {
    return ConstIntExpression(source.readInt())
  }

  if (current?.isLetter() == true) {
    return VariableIntExpression(source.readWord())
  }

  source.parseError("Expected number or variable, but was $current")
}
