package com.jonnyzzz.logo.web.reducers

import kotlinx.browser.localStorage
import kotlinx.serialization.StringFormat
import kotlinx.serialization.decodeFromString
import kotlinx.serialization.encodeToString
import org.w3c.dom.get
import org.w3c.dom.set
import react.RBuilder
import react.redux.provider
import react.redux.useSelector
import redux.RAction
import redux.createStore
import redux.rEnhancer

private val Json : StringFormat get() = kotlinx.serialization.json.Json {
  encodeDefaults = true
  ignoreUnknownKeys = true
}

private fun reducer(state: JogoState, action: RAction): JogoState = state.run {
  return when (action) {
    @Suppress("SENSELESS_NULL_IN_WHEN")
    null -> state

    @Suppress("SENSELESS_NULL_IN_WHEN")
    undefined -> state

    is StateAction -> action.run { update() }

    else -> state
  }
}

private const val localStorageKey = "jonnyzzz-jogo.2"

private fun loadState(): JogoState {
  val oldLocalState = runCatching {
    val stateText = localStorage[localStorageKey] ?: ""
    val state = Json.decodeFromString<JogoState>(stateText)
    state.copy()
  }.getOrNull()

  //TODO: keep older state to allow undo? What if two tabs were open?
  return oldLocalState ?: JogoState()
}

@Suppress("UnsafeCastFromDynamic")
private val store = createStore<JogoState, RAction, dynamic>(
  ::reducer,
  //https://github.com/lawik123/kotlin-poc-frontend-react-redux/issues/3#issuecomment-754718637
  loadState(),
//  compose(
    rEnhancer(),
//    js("if(window.__REDUX_DEVTOOLS_EXTENSION__ )window.__REDUX_DEVTOOLS_EXTENSION__ ();else(function(f){return f;});")
//  )
).apply {
  subscribe {
    localStorage[localStorageKey] = Json.encodeToString(getState())
  }
}

fun RBuilder.jogoProvider(builder: RBuilder.() -> Unit) {
  provider(store) {
    builder()
  }
}

inline fun <R> useJogoSelector(crossinline map: JogoState.() -> R): R {
  return useSelector<JogoState, R>({ it.map() }, { a, b -> a == b })
}
