package com.jonnyzzz.logo.maze

import kotlin.math.*

/**
 * Represents a point on the plan,
 * it could be start/stop/middle of a wall
 */
data class MazePoint(
  val x: Float,
  val y: Float,
)

/**
 * A wall is between two maze points
 */
data class MazeWall(
  val a: MazePoint,
  val b: MazePoint,
)

inline fun MazeWall.map(map : (MazePoint) -> MazePoint) = MazeWall(a = map(a), b = map(b))

/**
 * A collection of points (maybe not needed),
 * and a collection of all walls.
 *
 * There could be transformations to slightly move
 * points (and thus walls) later
 */
data class MazePointNet(
  val points: Set<MazePoint>,
  val walls: Set<MazeWall>,
)

/**
 * A minimal part of the Maze, aka cell.
 * Cell can be connected to other cells,
 * or may not be
 */
data class MazeCell(
  /** defines a polygon of all edges of a cell **/
  val points: Set<MazePoint>,

  /** lists all walls of a cell, some walls could be removed later **/
  val walls: Set<MazeWall>,
)

fun generateSquareMazePoints(
  topLeft: MazePoint,
  bottomRight: MazePoint,
  pointsX: Int,
  pointsY: Int,
): MazePointNet {
  val xMin = topLeft.x
  val xMax = bottomRight.x
  val xD = (xMax - xMin) / (pointsX - 1)

  val yMin = topLeft.y
  val yMax = bottomRight.y
  val yD = (yMax - yMin) / (pointsY - 1)

  val points = mutableMapOf<Pair<Int, Int>, MazePoint>()
  val walls = mutableSetOf<MazeWall>()

  repeat(pointsX) { xi ->
    repeat(pointsY) { yi ->
      points[xi to yi] = MazePoint(
        x = xMin + xD * xi,
        y = yMin + yD * yi,
      )
    }
  }

  fun buildWalls(outerN: Int, innerN: Int, index: (iOuter: Int, iInner: Int) -> Pair<Int, Int>) {
    repeat(outerN) { outerI ->
      //build walls
      repeat(innerN - 1) { innerI ->
        val prev = points[index(outerI, innerI)]
        val next = points[index(outerI, innerI + 1)]

        if (prev != null && next != null) {
          walls += MazeWall(prev, next)
        }
      }
    }
  }

  buildWalls(pointsX, pointsY) { iOuter, iInner -> Pair(iOuter, iInner) }
  buildWalls(pointsY, pointsX) { iOuter, iInner -> Pair(iInner, iOuter) }

  return MazePointNet(
    points = points.values.toSet(),
    walls = walls.toSet(),
  )
}

fun MazePointNet.bendV1(): MazePointNet {
  //TODO: why recompute here?
  val minX = points.minOf { it.x }
  val maxX = points.maxOf { it.x }
  val minY = points.minOf { it.y }
  val maxY = points.maxOf { it.y }

  val dx = (maxX - minX).absoluteValue.toDouble()
  val dy = (maxY - minY).absoluteValue.toDouble()

  val ddx = dx / 30
  val ddy = dy / 30

  return bend(this) {
    val ang = PI * dist((it.x - minX) / ddx, (it.y - minY) / ddy)
    val sin = sin(ang)
    val cos = cos(ang)

    MazePoint(
      x = (it.x.toDouble() + ddx * cos - ddy * sin).toFloat(),
      y = (it.y.toDouble() + ddx * sin + ddy * cos).toFloat(),
    )
  }
}

private fun dist(x: Double, y: Double) : Double = (x*x + y*y)

private fun bend(net: MazePointNet, bendFunction: (MazePoint) -> MazePoint) : MazePointNet {
  val oldToNew = net.points.associateWith(bendFunction)
  return MazePointNet(
    points = oldToNew.values.toSet(),
    walls = net.walls.map { it.map { p -> oldToNew[p]!! } }.toSet()
  )
}

