package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.script.*
import react.RBuilder
import react.dom.div

interface ExampleScriptGenerator : ScriptGenerator {
  val world: ScriptWorld
  fun startPos(x: Int, y: Int)

  fun world(sizeX: Int, sizeY: Int)
  fun tSize(t: Int)
}

fun RBuilder.svgExample(
  builder: ExampleScriptGenerator.() -> Unit
) {
  var startPos = ScriptPoint(0, 0)
  var world = ScriptWorld(100, 100, 10)
  val script = generateCode {
    object : ExampleScriptGenerator, ScriptGenerator by this {
      override val world get() = world
      override fun world(sizeX: Int, sizeY: Int) {
        world = ScriptWorld(sizeX, sizeY, world.tSize)
      }

      override fun tSize(t: Int) {
        world = world.copy(tSize = t)
      }

      override fun startPos(x: Int, y: Int) {
        startPos = ScriptPoint(x, y)
      }
    }.builder()
  }

  div(classes = "example") {
    div(classes = "svg") {
      child(SvgWorld) {
        attrs.world = world
        attrs.initialState = ScriptState.ZERO.copy(point = startPos)
        attrs.script = script
      }
    }

    div(classes = "code") {
      readonlyScriptEditorComponent(script.text)
    }
  }
}
