package com.jonnyzzz.logo.script


sealed class ScriptElement

class ScriptOnKeyHandlerToken(val key: VKey) : ScriptElement()
class ScriptRepeatToken(val times: Int) : ScriptElement()
object ScriptEndToken : ScriptElement()

sealed class ScriptCommand : ScriptElement() {
  abstract fun updateState(state: ScriptState) : ScriptState
}

/**
 * Marker interface for all time-related commands
 */
sealed interface ScriptTemporalAction

data class ScriptFrameDelayCommand(val delayMillis: IntExpression) : ScriptCommand(), ScriptTemporalAction {
  override fun updateState(state: ScriptState): ScriptState = state
}

object ScriptReplayCommand : ScriptCommand(), ScriptTemporalAction {
  override fun updateState(state: ScriptState): ScriptState = state
  override fun toString() = "ScriptReplayCommand"
}

data class ScriptArcCommand(val radius: IntExpression, val angle: IntExpression) : ScriptCommand() {
  override fun updateState(state: ScriptState) =
    state.copy(headDirection = normalizeAngle(state.headDirection + angle.evaluate(state)))
}

data class ScriptMoveCommand(val size: IntExpression) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    val sz = size.evaluate(state)
    val pX = sz * state.dX
    val pY = sz * state.dY
    return state.copy(point = ScriptPoint(x = state.x + pX.toInt(), y = state.y + pY.toInt()))
  }
}

data class ScriptTurnCommand(val angle: IntExpression) : ScriptCommand() {
  override fun updateState(state: ScriptState) =
    state.copy(headDirection = normalizeAngle(state.headDirection + angle.evaluate(state)))
}

object ScriptJumpHomeCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(point = ScriptPoint(x = 0, y = 0), headDirection = 0)
  }

  override fun toString() = "ScriptJumpHomeCommand"
}

data class ScriptHeadTurnCommand(val angle: IntExpression) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(headDirection = normalizeAngle(angle.evaluate(state)))
  }
}

object ScriptPenUpCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(penIsOn = false)
  }
  override fun toString() = "ScriptPenUpCommand"
}

object ScriptPenDownCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(penIsOn = true)
  }

  override fun toString() = "ScriptPenDownCommand"
}

object ScriptPushPos : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(stateStack = state.stateStack + state.state)
  }

  override fun toString() = "ScriptPushPos"
}

object ScriptPopPos: ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    val pos = state.stateStack.lastOrNull()
      ?: //TODO: report error?
      return state

    return state
      .copy(pos)
      .copy(stateStack = state.stateStack.dropLast(1))
  }
  override fun toString() = "ScriptPopPos"
}

object ScriptShowHeadCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(jogoIsVisible = true)
  }

  override fun toString() = "ScriptShowHeadCommand"
}

object ScriptHideHeadCommand : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(jogoIsVisible = false)
  }

  override fun toString() = "ScriptHideHeadCommand"
}

data class ScriptPenColorCommand(val color: PenColor) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(penColor = color)
  }
}

data class ScriptPenColorCommandRGB(val r: IntExpression, val b: IntExpression, val g: IntExpression) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(penColor = PenColor.RGB(
      r.evaluate(state).coerceIn(0, 255),
      b.evaluate(state).coerceIn(0, 255),
      g.evaluate(state).coerceIn(0, 255),
    ))
  }
}

data class ScriptSetCommand(val variable: String, var value: IntExpression) : ScriptCommand() {
  override fun updateState(state: ScriptState): ScriptState {
    return state.copy(intVariables = state.intVariables + (variable to value.evaluate(state)))
  }
}
