package com.jonnyzzz.logo.web

import com.jonnyzzz.logo.web.i18.Message
import com.jonnyzzz.logo.web.i18.msg
import com.jonnyzzz.logo.web.reducers.SharePlaneBase64
import com.jonnyzzz.logo.web.reducers.useJogoDispatch
import kotlinx.browser.window
import kotlinx.css.*
import react.Props
import react.fc
import react.useEffect
import react.useState
import styled.styledDiv

val LogoShareAcceptHeader = Message(en = "Loading Shared Work...")

private fun CssBuilder.mainComponentCss() {
  width = 80.pct
  padding(50.px)

  children("img") {
    width = 60.pct
  }
}

val LogoShareAccept = fc<Props> {
  val (errorMessage, setMessage) = useState { null as Message? }
  val dispatch = useJogoDispatch<SharePlaneBase64.LoadSharedStateAction>()
  val history = useJogoHistory()

  val hash = window.location.hash
  useEffect(listOf(hash)) {
    val updateState = SharePlaneBase64.handleShareLink(hash)

    if (updateState != null) {
      dispatch(updateState)
      history.pushStudio()
    } else {
      setMessage(Message(en = "Failed to process shared link, sorry"))
    }
  }

  styledDiv {
    css.mainComponentCss()
    if (errorMessage == null) {
      +"Loading..."
    } else {
      +"Failed to parse the share link. "
      msg(errorMessage)
    }
  }
}
