package com.jonnyzzz.logo.web

import kotlinx.coroutines.*
import react.useEffect

fun useSuspendEffect(
  coroutineName: CoroutineName,
  vararg context: dynamic,
  action: suspend CoroutineScope.() -> Unit
) {
  useEffect(*context) {
    val job = Job()

    cleanup {
      job.cancel()
    }

    val scope = object : CoroutineScope {
      override val coroutineContext = Dispatchers.Main + job + coroutineName
    }

    scope.launch(block = action)
  }
}
