package com.jonnyzzz.logo.web

import kotlinx.html.org.w3c.dom.events.Event
import react.router.useLocation
import react.router.useNavigate


object JogoAppRoutes {
  const val helpPath = "/help"
  const val sharePath = "/share"
  const val studioPath = "/"
}

class JogoHistory {
  private val location = useLocation()
  private val navigate = useNavigate()

  fun isHelpPage() = location.pathname.startsWith(JogoAppRoutes.helpPath)

  fun isStudio() = location.pathname.trim('/').isBlank()
  fun isSharePage() = location.pathname.startsWith(JogoAppRoutes.sharePath)

  fun pushShare(it: Event? = null) {
    it?.preventDefault()
    navigate(JogoAppRoutes.sharePath)
  }

  fun pushHelp(it: Event? = null) {
    it?.preventDefault()
    navigate(JogoAppRoutes.helpPath)
  }

  fun pushStudio(it: Event? = null) {
    it?.preventDefault()
    navigate(JogoAppRoutes.studioPath)
  }

  fun pushBack(it: Event? = null) {
    it?.preventDefault()
    navigate(-1)
  }
}

fun useJogoHistory() = JogoHistory()
