package com.jonnyzzz.logo.script

import kotlinx.coroutines.yield
import kotlinx.serialization.Serializable

@Serializable
data class VKey(val id: String) {
  companion object {
    val Space = VKey("Space")
    val Arrow_Up = VKey("Arrow_Up")
    val Arrow_Down = VKey("Arrow_Down")
    val Arrow_Left = VKey("Arrow_Left")
    val Arrow_Right = VKey("Arrow_Right")
  }
}

interface ScriptMachine {
  val commands: List<ScriptCommand>
}

data class ParsedScriptContext(
  override val commands: List<ScriptCommand>,

  val keyHandlers: Map<VKey, ScriptMachine>
) : ScriptMachine {

  fun applyKeys(keys: List<VKey>) = object : ScriptMachine {
    override val commands: List<ScriptCommand> =
      this@ParsedScriptContext.commands + keys.flatMap { keyHandlers[it]?.commands ?: listOf() }
  }
}

suspend fun compileScript(script: Script): ParsedScriptContext {
  val expressions = script
    .text
    .trim()
    .split("\n")
    .map { it.trim().split("#")[0].trim() }
    .map { it.trim().split("//")[0].trim() }
    .filter { it.isNotBlank() }
    .flatMap {
      yield()
      parseOneExpression(it)
    }

  val iterator = ScriptElementsIterator(expressions)
  return processExpressions(iterator)
}


private class ScriptElementsIterator(input: List<ScriptElement>) {
  private val iterator = input.iterator()

  fun next() = if (iterator.hasNext()) iterator.next() else null
}

private class ParsedScriptContextBuilder {
  private val default = mutableListOf<ScriptCommand>()
  private val map = mutableMapOf<VKey, MutableList<ScriptCommand>>()

  private var current : MutableList<ScriptCommand> = default

  fun setCurrent(vKey: VKey? = null) : Boolean {
    val isBase = current === default
    current = if (vKey == null) default else map.getOrPut(vKey) { mutableListOf() }
    return isBase
  }

  fun addCommand(cmd: ScriptCommand) {
    current.add(cmd)
  }

  fun addCommand(cmd: List<ScriptCommand>) {
    current.addAll(cmd)
  }

  fun build() = ParsedScriptContext(default.toList(), map.mapValues {
    object : ScriptMachine {
      override val commands: List<ScriptCommand> = it.value.toList()
    }
  })
}

private suspend fun processExpressions(expressions: ScriptElementsIterator): ParsedScriptContext {
  val builder = ParsedScriptContextBuilder()

  loop@ while (true) {
    yield()
    @Suppress("UNUSED_VARIABLE")
    val ignore : Any = when (val exp = expressions.next()) {
      null -> break@loop
      is ScriptEndToken -> {
        val hasDefault = builder.setCurrent(null)
        //TODO: here comes the validity check if this was the expected end
        if (hasDefault) {
          break@loop
        } else Unit
      }
      is ScriptOnKeyHandlerToken -> builder.setCurrent(exp.key)
      is ScriptCommand -> builder.addCommand(exp)
      is ScriptRepeatToken -> {
        val body = processExpressions(expressions).commands //TODO: assert?
        repeat(exp.times.coerceAtLeast(0)) {
          builder.addCommand(body)
        }
      }
    }
  }
  yield()
  return builder.build()
}

private inline fun String.matchExactIgnoreCase(prefix: String, action: () -> Unit) {
  if (this.trim().equals(prefix, ignoreCase = true)) {
    return action()
  }
}

private inline fun String.matchPrefixIgnoreCase(prefix: String, action: (String) -> Unit) {
  if (this.startsWith(prefix, ignoreCase = true)) {
    val substring = this.substring(prefix.length)
    if (substring.isNotEmpty() && substring.first().isWhitespace()) {
      return action(substring.trim())
    }
  }
}

private fun parseOneExpression(command: String): List<ScriptElement> {
  if (command.trim() != command) return parseOneExpression(command.trim())

  command.matchPrefixIgnoreCase("on") {
    it.matchPrefixIgnoreCase("key") {
      it.matchExactIgnoreCase("up") {
        return listOf(ScriptOnKeyHandlerToken(VKey.Arrow_Up))
      }
      it.matchExactIgnoreCase("down") {
        return listOf(ScriptOnKeyHandlerToken(VKey.Arrow_Down))
      }
      it.matchExactIgnoreCase("left") {
        return listOf(ScriptOnKeyHandlerToken(VKey.Arrow_Left))
      }
      it.matchExactIgnoreCase("right") {
        return listOf(ScriptOnKeyHandlerToken(VKey.Arrow_Right))
      }
      it.matchExactIgnoreCase("space") {
        return listOf(ScriptOnKeyHandlerToken(VKey.Space))
      }
    }
  }

  command.matchExactIgnoreCase("replay") {
    return listOf(ScriptReplayCommand)
  }

  command.matchPrefixIgnoreCase("repeat") {
    val times = it.trim().toIntOrNull()?.takeIf { it > 0 } ?: return listOf()
    return listOf(ScriptRepeatToken(times))
  }

  command.matchExactIgnoreCase("end") {
    return listOf(ScriptEndToken)
  }

  command.matchPrefixIgnoreCase("fd") {
    val sz = runCatching {
      parseExpression(ExpressionTokenSource(it))
    }.getOrNull() ?: return emptyList()
    return listOf(ScriptMoveCommand(sz))
  }

  command.matchPrefixIgnoreCase("lt") {
    val angle = runCatching {
      parseExpression(ExpressionTokenSource(it))
    }.getOrNull() ?: return emptyList()
    return listOf(ScriptTurnCommand(NegateIntExpression(angle)))
  }

  command.matchPrefixIgnoreCase("rt") {
    val angle = runCatching {
      parseExpression(ExpressionTokenSource(it))
    }.getOrNull() ?: return emptyList()
    return listOf(ScriptTurnCommand(angle))
  }

  command.matchExactIgnoreCase("pu") {
    return listOf(ScriptPenUpCommand)
  }

  command.matchExactIgnoreCase("pd") {
    return listOf(ScriptPenDownCommand)
  }

  command.matchExactIgnoreCase("home") {
    return listOf(ScriptJumpHomeCommand)
  }

  command.matchExactIgnoreCase("head") {
    return listOf(ScriptHeadTurnCommand(ConstIntExpression(0)))
  }

  command.matchPrefixIgnoreCase("head") {
    val angle = runCatching {
      parseExpression(ExpressionTokenSource(it))
    }.getOrNull() ?: return emptyList()
    return listOf(ScriptHeadTurnCommand(angle))
  }

  command.matchPrefixIgnoreCase("arc") {
    val (a, b) = runCatching {
      parseExpressionList(ExpressionTokenSource(it), minElements = 2, maxElements = 2)
    }.getOrNull() ?: return emptyList()

    return listOf(ScriptArcCommand(a, b))
  }

  command.matchPrefixIgnoreCase("circle") {
    val r = runCatching {
      parseExpression(ExpressionTokenSource(it))
    }.getOrNull() ?: return emptyList()
    return listOf(ScriptArcCommand(r, ConstIntExpression(360)))
  }

  command.matchExactIgnoreCase("frame") {
    return listOf(ScriptFrameDelayCommand(ConstIntExpression(1000)))
  }

  command.matchPrefixIgnoreCase("frame") {
    val delay = runCatching {
      parseExpression(ExpressionTokenSource(it))
    }.getOrNull() ?: return emptyList()

    return listOf(ScriptFrameDelayCommand(delay))
  }

  command.matchPrefixIgnoreCase("push") {
    it.matchExactIgnoreCase("pos") {
      return listOf(ScriptPushPos)
    }
  }

  command.matchPrefixIgnoreCase("pop") {
    it.matchExactIgnoreCase("pos") {
      return listOf(ScriptPopPos)
    }
  }

  command.matchExactIgnoreCase("show") {
    return listOf(ScriptShowHeadCommand)
  }

  command.matchPrefixIgnoreCase("show") {
    it.matchExactIgnoreCase("jogo") {
      return listOf(ScriptShowHeadCommand)
    }
  }

  command.matchExactIgnoreCase("hide") {
    return listOf(ScriptHideHeadCommand)
  }

  command.matchPrefixIgnoreCase("hide") {
    it.matchExactIgnoreCase("jogo") {
      return listOf(ScriptHideHeadCommand)
    }
  }

  command.matchPrefixIgnoreCase("color") {
    it.matchExactIgnoreCase("black") {
      return listOf(ScriptPenColorCommand(PenColor.BLACK))
    }

    it.matchExactIgnoreCase("blue") {
      return listOf(ScriptPenColorCommand(PenColor.BLUE))
    }

    it.matchExactIgnoreCase("green") {
      return listOf(ScriptPenColorCommand(PenColor.GREEN))
    }

    it.matchExactIgnoreCase("red") {
      return listOf(ScriptPenColorCommand(PenColor.RED))
    }

    it.matchPrefixIgnoreCase("rgb") {
      val (r, g, b) = runCatching {
        parseExpressionList(ExpressionTokenSource(it), minElements = 3, maxElements = 3)
      }.getOrNull() ?: return emptyList()
      return listOf(ScriptPenColorCommandRGB(r, g, b))
    }
  }

  command.matchPrefixIgnoreCase("set") {
    val varAndExpr = it.split("=", limit = 2).map { it.trim() }
    if (varAndExpr.size != 2) return emptyList()
    val (varName, expr) = varAndExpr
    val exprNode = runCatching {
      parseExpression(ExpressionTokenSource(expr))
    }.getOrNull() ?: return emptyList()

    return listOf(ScriptSetCommand(varName, exprNode))
  }

  return listOf()
}
