package com.jonnyzzz.logo.script


enum class ExpressionListOp {
  //replace here with builders to include more detailed error messages
  ACCEPT,
  FAIL,
  STOP
}

fun <R> parseOpList(
  source: ExpressionTokenSource,
  parseExpression: (ExpressionTokenSource) -> IntExpression,
  parseOperator: (Char) -> ExpressionListOp,
  acc: (IntExpression) -> R,
  reduce: (acc: R, op: Char, next: IntExpression) -> R,
): R {
  var result = acc(parseExpression(source))
  while (true) {
    source.skipWhitespaces()
    if (!source.hasMoreTokens()) break
    val current = source.current ?: break
    val ignore = when (parseOperator(current)) {
      ExpressionListOp.FAIL -> source.parseError("Unexpected operator $current")
      ExpressionListOp.STOP -> break
      ExpressionListOp.ACCEPT -> 42
    }
    source.next()
    //handle dangling ',' just link in Golang
    source.skipWhitespaces()
    if (!source.hasMoreTokens()) break
    result = reduce(result, current, parseExpression(source))
  }

  return result
}

