package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.*
import com.jonnyzzz.logo.web.css.JogoStyle.jetbrainsMono
import com.jonnyzzz.logo.web.css.JogoStyle.mainBorder
import com.jonnyzzz.logo.web.reducers.JogoState
import com.jonnyzzz.logo.web.reducers.StateAction
import com.jonnyzzz.logo.web.reducers.useJogoDispatch
import com.jonnyzzz.logo.web.reducers.useJogoSelector
import kotlinx.css.*
import org.w3c.dom.HTMLDivElement
import react.*
import react.dom.div
import styled.styledDiv

private sealed interface State3Action : StateAction

private data class UpdateState3(
  private val newState: CodeMirrorState,
  private val newText: String? = null,
) : State3Action {
  override fun JogoState.update() = copy(scriptEditorState = newState, script = newText ?: script)
}

private fun createNewState(scriptText: String): CodeMirrorState {
  val selection = when {
    scriptText.isNotBlank() -> codeMirrorSelectionCreate(codeMirrorSelectionCursor(scriptText.length))
    else -> null
  }

  return codeMirrorState(
    doc = scriptText,
    selection = selection
  )
}

val ScriptEditor3 = fc<Props> {
  val scriptText = useJogoSelector { this.script }
  val scriptEditorState = useJogoSelector { this.scriptEditorState }
    ?.takeIf { it.sliceDoc() == scriptText }
    ?: createNewState(scriptText)

  val dispatcher = useJogoDispatch<State3Action>()

  child<ScriptEditorComponentProps3, ScriptEditorComponent3> {
    attrs {
      state = scriptEditorState
      onStateChange = { it, state ->
        var update = UpdateState3(state)
        if (it.docChanged) {
          update = update.copy(newText = it.state.sliceDoc())
        }
        dispatcher(update)
      }
    }
  }
}

private external interface ScriptEditorComponentProps3 : Props {
  var state: CodeMirrorState
  var onStateChange: (CodeMirrorTransaction, CodeMirrorState) -> Unit
}

private class ScriptEditorComponent3 : RComponent<ScriptEditorComponentProps3, State>() {
  private val parentRoot = createRef<HTMLDivElement>()
  private var codeMirrorView: CodeMirrorView? = null

  override fun componentDidMount() {
    codeMirrorView = CodeMirrorView(
      state = props.state,
      parent = parentRoot.current!!,
      dispatcher = {
        codeMirrorView?.let { view ->
          view.update(arrayOf(it))
          props.onStateChange(it, view.state)
        }
      }
    )

    codeMirrorView?.focus()
  }

  override fun componentDidUpdate(prevProps: ScriptEditorComponentProps3, prevState: State, snapshot: Any) {
    codeMirrorView?.let { view ->
      if (view.state != props.state) {
        println("Editor State has changed from outside")
        view.setState(props.state)
      }
    }
  }

  override fun componentWillUnmount() {
    codeMirrorView?.destroy()
    codeMirrorView = null
  }

  private fun CssBuilder.mainComponentCss() {
    mainBorder(this)
    height = 100.pct
    jetbrainsMono(this)
    fontSize = 120.pct

    descendants(".cm-editor") {
      height = 100.pct
    }

    children(".theEditor") {
      height = 100.pct
      overflowY = Overflow.scroll
    }
  }

  override fun RBuilder.render() {
    styledDiv {
      css.mainComponentCss()
      div("theEditor") {
        ref = parentRoot
      }
    }
  }
}
