package com.jonnyzzz.logo.web

import kotlinx.css.*
import kotlinx.css.properties.borderBottom
import react.Props
import react.dom.a
import react.fc
import styled.styledDiv

private fun CssBuilder.mainComponentCss() {
  fontSize = 80.pct
  textAlign = TextAlign.center

  paddingTop = 10.px

  "a" {
    borderBottom(1.px, BorderStyle.dashed, Color.lightSlateGray)
  }
}

val LogoFooter = fc<Props> {
  styledDiv {
    css.mainComponentCss()

    +"© Eugene Petrenko 2021-2022. For more information, please contact me at jonnyzzz.com. "
    +"Powered by "
    a(href = "https://kotlinlang.org/docs/js-overview.html") { +"Kotlin/JS" }
    +" and "
    a(href = "https://reactjs.org/") { +"React" }
    + ". "
    a(href = "https://forms.gle/2UyhVFrYf1k9W9te9") {
      +"Feedback"
    }
  }
}
