package com.jonnyzzz.logo.web.components

import kotlinx.css.*
import react.*
import styled.css
import styled.styledSpan

@JsModule("icons/help.svg")
private external object HelpIconWrapper {
  @JsName("default")
  class HelpIcon : Component<dynamic, dynamic> {
    override fun render(): ReactElement<*>?
  }
}

@JsModule("icons/jogo.svg")
private external object JogoIconWrapper {
  @JsName("default")
  class HelpIcon : Component<dynamic, dynamic> {
    override fun render(): ReactElement<*>?
  }
}

@JsModule("icons/trash.svg")
private external object TrashIconWrapper {
  @JsName("default")
  class HelpIcon : Component<dynamic, dynamic> {
    override fun render(): ReactElement<*>?
  }
}

enum class SvgIcon {
  HELP,
  JOGO,
  TRASH,
}

fun RBuilder.svgIcon(icon: SvgIcon, size: Int = 16, horizontalPadding: Int = 0) {
  styledSpan {
    css {
      child("svg") {
        width = size.px
        height = size.px
        paddingLeft = horizontalPadding.px
        paddingRight = horizontalPadding.px
      }
    }

    when(icon) {
      SvgIcon.HELP -> HelpIconWrapper.HelpIcon::class
      SvgIcon.JOGO -> JogoIconWrapper.HelpIcon::class
      SvgIcon.TRASH -> TrashIconWrapper.HelpIcon::class
    }.react { }
  }
}
