package com.jonnyzzz.logo.script

import kotlinx.serialization.Serializable
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json
import kotlin.math.PI
import kotlin.math.absoluteValue
import kotlin.math.cos
import kotlin.math.sin


@Serializable
sealed class PenColor {
  companion object {
    val BLACK = RGB(0, 0, 0)
    val RED = RGB(255, 0, 0)
    val BLUE = RGB(0, 0, 255)
    val GREEN = RGB(0, 255, 0)
    val WHITE = RGB(255, 255, 255)
  }
  data class RGB(val r: Int, val g: Int, val b: Int) : PenColor()
}

@Serializable
data class ScriptWorld(
  val topLeft : ScriptPoint,
  val bottomRight: ScriptPoint,
  val tSize: Int
) {
  val sizeX get() = (bottomRight.x - topLeft.x).absoluteValue
  val sizeY get() = (bottomRight.y - topLeft.y).absoluteValue

  fun toViewBox() = "${topLeft.x} ${topLeft.y} $sizeX $sizeY"

  @Suppress("EXPERIMENTAL_API_USAGE")
  fun toReactStateHash() = Json.encodeToString(this)
}

fun ScriptWorld(sizeX: Int, sizeY: Int, tSize: Int = (sizeX + sizeY) / 75): ScriptWorld {
  return ScriptWorld(
    topLeft = ScriptPoint(-sizeX / 2, -sizeY / 2),
    bottomRight = ScriptPoint(sizeX / 2, sizeY / 2),
    tSize = tSize
  )
}

@Serializable
data class ScriptPoint(val x: Int, val y: Int) {
  companion object {
    val ZERO = ScriptPoint(0, 0)
  }
}

@Serializable
data class ScriptPositionState(
  val point : ScriptPoint = ScriptPoint.ZERO,
  val headDirection: Int = 0,
)

@Serializable
@Suppress("DataClassPrivateConstructor")
data class ScriptState private constructor(
  val point: ScriptPoint = ScriptPoint.ZERO,
  val headDirection: Int = 0,

  val penColor: PenColor = PenColor.BLACK,
  val penIsOn: Boolean = true,
  val jogoIsVisible: Boolean = true,

  val stateStack : List<ScriptPositionState> = listOf(),
  val intVariables: Map<String, Int> = mapOf(),
) {

  companion object {
    val ZERO: ScriptState get() = ScriptState()
  }

  val state get() = ScriptPositionState(point, headDirection)
  fun copy(state : ScriptPositionState) = copy(point = state.point, headDirection = state.headDirection)

  val x get() = point.x
  val y get() = point.y

  private val headAngle get() = 2 * PI * headDirection / 360
  val dX get() = sin(headAngle)
  val dY get() = -cos(headAngle)

  @Suppress("EXPERIMENTAL_API_USAGE")
  fun toReactStateHash() = Json.encodeToString(this)
}
