package com.jonnyzzz.logo.web

import kotlinx.js.jso
import org.w3c.dom.HTMLElement

external class CodeMirrorText {
  val length : Number
  val lines : Number

  companion object {
    fun of(text: String) : CodeMirrorText
  }
}

external interface CodeMirrorTransaction {
  val docChanged: Boolean

  val state: CodeMirrorState
  val newDoc: CodeMirrorText
}

external interface CodeMirrorState {
  val doc: CodeMirrorText

  fun sliceDoc(from: Number = definedExternally, to: Number = definedExternally) : String
}

external interface CodeMirrorSelection {

}

external interface CodeMirrorSelectionRange {

}


private val CodeMirrorSelectionImpl: dynamic
  = kotlinext.js.require("@codemirror/state").EditorSelection

fun codeMirrorSelectionCursor(pos: Number) : CodeMirrorSelectionRange {
  @Suppress("UnsafeCastFromDynamic")
  return CodeMirrorSelectionImpl.cursor(pos)
}

fun codeMirrorSelectionCreate(vararg ranges: CodeMirrorSelectionRange) : CodeMirrorSelection {
  @Suppress("UnsafeCastFromDynamic")
  return CodeMirrorSelectionImpl.create(ranges)
}

private val CodeMirrorStateImpl: dynamic
  = kotlinext.js.require("@codemirror/state").EditorState

@JsModule("@codemirror/commands")
@JsName("defaultKeymap")
external val codeMirrorDefaultKeymap: dynamic

private val codeMirrorHistoryImpl : () -> dynamic
    = kotlinext.js.require("@codemirror/commands").history

private val codeMirrorViewImpl : dynamic
    = kotlinext.js.require("@codemirror/view")

private val CodeMirrorKeymapImpl : dynamic
  = codeMirrorViewImpl.keymap

private fun codeMirrorKeymapOf(keymap: dynamic) : dynamic {
  return CodeMirrorKeymapImpl.of(keymap)
}

fun codeMirrorReadonlyState(
  doc: String,
): CodeMirrorState {
  return codeMirrorStateImpl(doc, extensions = arrayOf(
    codeMirrorViewImpl.editable.of(false),
  ))
}

fun codeMirrorState(
  doc: String,
  extensions: Array<dynamic> = arrayOf(),
  selection: CodeMirrorSelection? = null,
  ): CodeMirrorState {
  val mergedExtensions = arrayOf<dynamic>(
    codeMirrorKeymapOf(codeMirrorDefaultKeymap),
    //https://codemirror.net/docs/ref/#commands.history
    codeMirrorHistoryImpl(),
    //https://codemirror.net/docs/ref/#view.highlightActiveLine
    codeMirrorViewImpl.highlightActiveLine(),
    *extensions
  )

  return codeMirrorStateImpl(doc, mergedExtensions, selection)
}

private fun codeMirrorStateImpl(
  doc: String,
  extensions: Array<dynamic> = arrayOf(),
  selection: CodeMirrorSelection? = null,
): CodeMirrorState {
  val jso = jso<dynamic> {
    this.doc = doc
    this.extensions = extensions
    this.selection = selection
  }
  return CodeMirrorStateImpl.create(jso)
}

external class CodeMirrorView {
  val state: CodeMirrorState
  fun setState(newState: CodeMirrorState)

  fun focus()
  fun destroy()
  fun dispatch(transaction: CodeMirrorTransaction)
  fun update(transactions: Array<CodeMirrorTransaction>)
}

private val CodeMirrorViewClazz : dynamic
  = kotlinext.js.require("@codemirror/view").EditorView

fun CodeMirrorView(
   state: CodeMirrorState,
   parent: HTMLElement,
   dispatcher: (CodeMirrorTransaction) -> Unit,
) : CodeMirrorView {
  @Suppress("UNUSED_VARIABLE")
  val params : dynamic = jso {
    this.state = state
    this.parent = parent
    this.dispatch = dispatcher
  }
  @Suppress("UNUSED_VARIABLE")
  val clazz = CodeMirrorViewClazz
  return js("new clazz(params)")
}


