package com.jonnyzzz.logo.script

import kotlinx.serialization.Serializable
import kotlinx.serialization.encodeToString
import kotlinx.serialization.json.Json

interface ScriptGenerator {
  fun fd(v: String)
  fun fd(v: Int) = fd(v.toString())
  fun lt(a: String)
  fun lt(a: Int) = lt(a.toString())
  fun pu()
  fun pd()
  fun repeat(num: Int, builder: ScriptGenerator.() -> Unit)
  fun onKey(key: String, builder: ScriptGenerator.() -> Unit)
  fun arc(d: Int, a: Int)
  fun circle(d: Int)
  fun hide()
  fun show()
  fun pushPos()
  fun popPos()
  fun frame(ms: Int)
  fun replay()
  fun color(color: String)
  fun colorRgb(r: Int, g: Int, b: Int)
  fun set(variable: String, expr: String)
  operator fun String.unaryPlus()
}

fun generateCode(
  builder: ScriptGenerator.() -> Unit,
) : Script {
  val code = buildString {
    generateCode("", builder)
  }
  return Script(code)
}

@Serializable
data class Script(
  val text: String
) {
  @Suppress("EXPERIMENTAL_API_USAGE")
  fun toReactStateHash() = Json.encodeToString(this)
}

private fun StringBuilder.generateCode(indent: String, builder: ScriptGenerator.() -> Unit) {
  object: ScriptGenerator {
    override fun pu() { appendLine("${indent}pu") }
    override fun pd() { appendLine("${indent}pd") }
    override fun arc(d: Int, a: Int) { appendLine("${indent}arc $d, $a") }
    override fun circle(d: Int) { appendLine("${indent}circle $d") }
    override fun fd(v: String) { appendLine("${indent}fd $v") }
    override fun lt(a: String) { appendLine("${indent}lt $a") }
    override fun hide() { appendLine("${indent}hide") }
    override fun show() { appendLine("${indent}show") }
    override fun pushPos() { appendLine("${indent}push pos") }
    override fun popPos() { appendLine("${indent}pop pos") }
    override fun frame(ms: Int) { appendLine("${indent}frame $ms") }
    override fun replay() { appendLine("${indent}replay") }
    override fun color(color: String) { appendLine("${indent}color $color") }
    override fun colorRgb(r: Int, g: Int, b: Int) { appendLine("${indent}color rgb $r, $g, $b") }
    override fun set(variable: String, expr: String) { appendLine("${indent}set $variable = $expr")}

    override fun repeat(num: Int, builder: ScriptGenerator.() -> Unit) {
      appendLine("${indent}repeat $num")
      generateCode("  $indent", builder)
      appendLine("${indent}end")
    }
    override fun onKey(key: String, builder: ScriptGenerator.() -> Unit) {
      appendLine("${indent}on key $key")
      generateCode("  $indent", builder)
      appendLine("${indent}end")
    }

    override fun String.unaryPlus() {
      split("\n").map { it.trim() }.forEach { appendLine("${indent}$it") }
    }
  }.builder()
}
