package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.i18.Message
import com.jonnyzzz.logo.web.i18.Messages
import com.jonnyzzz.logo.web.i18.msg
import react.RBuilder
import react.dom.*

interface LegendDocumentationBuilder {
  fun shortEntry(
    code: String,
    example: String?,
    explanation: Message,
  )
}

fun RBuilder.legendDocumentation(
  contents: LegendDocumentationBuilder.() -> Unit
) {
  table("documentation-table") {
    thead {
      tr {
        th {
          msg(Messages.docTableCommandTitle)
        }
        th {
          msg(Messages.docTableCommandTitle)
        }
      }
    }
    tbody {
      val builder = this
      object : LegendDocumentationBuilder {
        override fun shortEntry(code: String, example: String?, explanation: Message) {
          builder.shortEntry(code, example, explanation)
        }
      }.contents()
    }
  }
}

private fun RBuilder.shortEntry(
  code: String,
  example: String?,
  explanation: Message,
) {
  tr {
    td("code") {
      code(code)
    }
    td("exp") {
      msg(explanation)
      if (example != null) {
        br { }
        code(example)
      }
    }
  }
}

private fun RBuilder.code(code: String) {
  pre {
    +code
  }
}
