package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.i18.Message
import com.jonnyzzz.logo.web.i18.Messages
import com.jonnyzzz.logo.web.i18.msg
import react.RBuilder
import react.dom.*


interface CommandListBuilder {
  fun command(command: String, explanation: Message, example: (ExampleScriptGenerator.() -> Unit)? = null)
}

fun RBuilder.commandDocumentation(
  contents: CommandListBuilder.() -> Unit
) {
  table("documentation-table") {
    thead {
      tr {
        th {
          msg(Messages.docTableCommandTitle)
        }
        th {
          msg(Messages.docTableCommandDescription)
        }
      }
    }
    tbody {
      val builder = this
      object : CommandListBuilder {
        override fun command(command: String, explanation: Message, example: (ExampleScriptGenerator.() -> Unit)?) {
          builder.doc(command, example, explanation)
        }
      }.contents()
    }
  }
}

private fun RBuilder.doc(
  command: String,
  example: (ExampleScriptGenerator.() -> Unit)?,
  explanation: Message
) {
  tr {
    td(classes = "code") {
      pre {
        +command
      }
    }

    td(classes = "exp") {
      msg(explanation)

      if (example != null) {
        svgExample {
          world(100, 100)
          tSize(10)

          example()
        }
      }
    }
  }
}
