package com.jonnyzzz.logo.web.reducers

import com.jonnyzzz.logo.maze.MazePoint
import com.jonnyzzz.logo.maze.generateSquareMazePoints
import com.jonnyzzz.logo.script.ScriptPoint
import com.jonnyzzz.logo.web.i18.JogoLocalization
import react.redux.useDispatch
import redux.RAction


interface StateAction : RAction {
  fun JogoState.update(): JogoState
}

inline fun <reified R: StateAction> useJogoDispatch(): (R) -> Unit {
  return useDispatch()
}

data class ChangeLocaleAction(
  val newLocale: JogoLocalization
) : StateAction {
  override fun JogoState.update(): JogoState = copy(locale = newLocale)
}

class ScriptResetAllAction: StateAction {
  override fun JogoState.update(): JogoState = JogoState(locale = locale)
}

class ScriptClearAllAction: StateAction {
  override fun JogoState.update(): JogoState = copy(script = "", maze = null)
}

class GenerateNewMazeAction : StateAction {
  override fun JogoState.update(): JogoState {
    val newMaze = generateSquareMazePoints(
      world.topLeft.toMazePoint(),
      world.bottomRight.toMazePoint(),
      world.sizeX/(2*world.tSize + 5),
      world.sizeY/(2*world.tSize + 5),
    )
    return copy(maze = newMaze)
  }
}

fun ScriptPoint.toMazePoint() = MazePoint(x.toFloat(), y.toFloat())
