package com.jonnyzzz.logo.web.reducers

import com.jonnyzzz.logo.script.ScriptWorld
import com.jonnyzzz.logo.web.base64Decode
import com.jonnyzzz.logo.web.base64EncodeURI
import kotlinx.browser.window
import kotlinx.serialization.Serializable
import kotlinx.serialization.StringFormat
import kotlinx.serialization.decodeFromString
import kotlinx.serialization.encodeToString
import org.w3c.dom.url.URL

object SharePlaneBase64 {
  private const val shareLinkPrefix = "#/share/base64/"

  @Serializable
  private data class SharedJogoState(
    val script: String,
    val world: ScriptWorld
  )

  private val Json : StringFormat get() = kotlinx.serialization.json.Json {
    encodeDefaults = true
    ignoreUnknownKeys = true
  }

  private fun SharedJogoState.applyTo(state: JogoState): JogoState {
    return state.copy(script = script, world = world)
  }

  fun generateUrl(state: JogoState) : String {
    return URL(generateRelativeUrl(state), window.location.href).href
  }

  private fun generateRelativeUrl(state: JogoState) : String {
    //TODO: make it compute the hash lazily
    val serialized = Json.encodeToString(state)
    return "/" + shareLinkPrefix + base64EncodeURI(serialized)
  }

  fun handleShareLink(hash: String) : LoadSharedStateAction? {
    if (!hash.startsWith(shareLinkPrefix)) return null

    val share : SharedJogoState =  try {
      Json.decodeFromString(base64Decode(hash.removePrefix(shareLinkPrefix)))
    } catch (t: Throwable) {
      console.log("Failed to decode state: ${t.message}")
      return null
    }

    return LoadSharedStateActionImpl(share)
  }

  abstract class LoadSharedStateAction : StateAction

  private class LoadSharedStateActionImpl(val share: SharedJogoState) : LoadSharedStateAction(), StateAction {
    override fun JogoState.update(): JogoState = share.applyTo(this)
  }
}
