package com.jonnyzzz.logo.web.components

import com.jonnyzzz.logo.web.css.JogoStyle
import kotlinx.css.CssBuilder
import kotlinx.css.height
import kotlinx.css.pct
import kotlinx.css.width
import react.Props
import react.RBuilder
import react.RComponent
import react.State
import react.dom.value
import styled.styledTextarea

fun RBuilder.readonlyScriptEditorComponent(text: String) {
  child(ScriptEditor::class) {
    attrs {
      this.text = text
    }
  }
}

private external interface ScriptEditorProps : Props {
  var text: String
}

private class ScriptEditor : RComponent<ScriptEditorProps, State>() {
  private fun CssBuilder.mainComponentCSS() {
    JogoStyle.jetbrainsMono(this)
    width = 100.pct
    height = 100.pct
  }

  override fun RBuilder.render() {
    styledTextarea {
      css.mainComponentCSS()

      val defaultCols = 20
      val defaultRows = 4

      val cols = run {
        val actual = props.text.split("\n").maxOfOrNull { it.trim().length } ?: 0
        (3 + actual).coerceIn(5, defaultCols)
      }

      val rows = props.text.count { it == '\n' }.coerceAtLeast(defaultRows)

      attrs.readonly = true
      attrs.rows = rows.toString()
      attrs.cols = cols.toString()
      attrs.value = props.text
    }
  }
}
