import kotlin.js.json

external interface SvgWorldProps : Props {
  var world: ScriptWorld
  var initialState: ScriptState?
  var script: Script
  var maze: MazePointNet?
}

val SvgWorldMainComponent = fc<Props> {
  val script = useJogoSelector { script }
  val world = useJogoSelector { world }
  val maze = useJogoSelector { maze }

  child(SvgWorld) {
    attrs {
      this.script = Script(script)
      this.world = world
      this.maze = maze
      this.initialState = null
    }
  }
}

val SvgWorld = fc<SvgWorldProps> { props ->
  val script = props.script
  val (processedScript, setProcessedScript) = useState<ParsedScriptContext?> { null }

  //TODO: we need this process to be one level up to allow
  //TODO: the editor to access the ParsedScriptContext
  //TODO: which later will have errors information
  useSuspendEffect(CoroutineName("script compiler"), script.toReactStateHash()) {
    //it may be started after component is created, so resetting it first
    setProcessedScript(null)

    yield()

    val context = compileScript(script)

    yield()
    setProcessedScript(context)
  }

  if (processedScript == null) {
    svgViewWrapper(props.world) {
      //TODO: aka loading
      jogoHead(ScriptState.ZERO, 64)
    }
  } else {
    child(SvgProcessedWorld) {
      attrs.world = props.world
      attrs.initialState = props.initialState
      attrs.maze = props.maze
      attrs.processedScript = processedScript
    }
  }
}

private external interface SvgProcessedWorldProps : Props {
  var processedScript: ParsedScriptContext
  var world: ScriptWorld
  var initialState: ScriptState?
  var maze: MazePointNet?
}

private val SvgProcessedWorld = fc<SvgProcessedWorldProps> { props ->
  val world = props.world
  val initialState = props.initialState ?: ScriptState.ZERO
  val compiledScript = props.processedScript

  var keys by useState<List<VKey>> { listOf() }
  val (snapshot, setSnapshot) = useState<WorldSnapshot?>(null)
  useSuspendEffect(CoroutineName("UI"),
    keys, world.toReactStateHash(), initialState.toReactStateHash(), compiledScript) {
    val reactiveWorld = ReactiveWorldPlayer(world, initialState, compiledScript.applyKeys(keys))

    launch {
      reactiveWorld.changes.collect {
        launch {
          setSnapshot(it)
        }
      }
    }

    reactiveWorld.apply { start() }
  }

  val onKeyPress = x@{ it: Event ->
    it.preventDefault()
    val d: dynamic = it
    val keyCode = d.keyCode?.toString() ?: return@x
    val vkey = run {
      when (keyCode) {
        "38" -> VKey.Arrow_Up
        "40" -> VKey.Arrow_Down
        "37" -> VKey.Arrow_Left
        "39" -> VKey.Arrow_Right
        else -> null
      }
    } ?: run {
      val key = d.key?.toString() ?: return@x
      when (key) {
        " " -> VKey.Space
        else -> null
      }
    } ?: return@x
    println("on vkey: $vkey")
    keys = keys + vkey
  }

  svgViewWrapper(world) {
    attrs.tabIndex = "42"
    attrs.onKeyDownFunction = onKeyPress

    props.maze?.let { mazeProps ->
      child(MazeComponent) {
        attrs.maze = mazeProps
      }
    }

    if (snapshot != null) {
      for (jSvg in snapshot.svg) {
        toSvg(jSvg)
      }

      jogoHead(snapshot)
    }
  }
}

private fun RBuilder.svgViewWrapper(world: ScriptWorld, content: StyledDOMBuilder<SVG>.() -> Unit) {
  styledSvg {
    mainBorder(css)
    css.focus {
      mainBorder(this)
      outline = Outline.none
    }

    // https://developer.mozilla.org/en-US/docs/Web/SVG/Attribute/viewBox
    // minX, minY, width, height
    attrs["width"] = "100%"
    attrs["viewBox"] = world.toViewBox()

    content()
  }
}

private fun RBuilder.jogoHead(state: WorldSnapshot) {
  jogoHead(state.finalState, state.world.tSize)
}

private fun RBuilder.jogoHead(pos: ScriptState, tSize: Int) {
  if (!pos.jogoIsVisible) return

  custom("circle") {
    attrs["cx"] = pos.x
    attrs["cy"] = pos.y
    attrs["r"] = tSize
    attrs["style"] = json(
      "stroke" to "black",
      "fill" to "red",
    )
  }

  custom("line") {
    attrs["x1"] = pos.x
    attrs["y1"] = pos.y
    attrs["x2"] = pos.x + tSize * pos.dX
    attrs["y2"] = pos.y + tSize * pos.dY
    attrs["strokeWidth"] = "2px"
    attrs["stroke"] = "black"
  }
}

private fun RBuilder.toSvg(svg: JSvgElement) {
  when (svg) {
    is JSvgLineElement -> toSvg(svg)
    is JSvgCircleElement -> toSvg(svg)
    is JSvgPathElement -> toSvg(svg)
    else -> error("Unknown element: $svg")
  }
}

private inline fun <T> inlineForEach(t1: T, t2: T, p: (T) -> Unit) { p(t1); p(t2) }

private fun RBuilder.toSvg(svg: JSvgLineElement) {
  custom("line") {
    attrs["x1"] = svg.from.x
    attrs["y1"] = svg.from.y
    attrs["x2"] = svg.to.x
    attrs["y2"] = svg.to.y
    attrs["stroke"] = svg.penColor.toSvgStroke()
    attrs["strokeWidth"] = /*if (script.hover) "4px" else*/ "2px"
  }

  inlineForEach(svg.from, svg.to) { p ->
    custom("circle") {
      attrs["r"] = "1"
      attrs["cx"] = p.x
      attrs["cy"] = p.y
      attrs["fill"] = svg.penColor.toSvgStroke()
    }
  }
}

private fun RBuilder.toSvg(svg: JSvgCircleElement) {
  custom("circle") {
    attrs["r"] = svg.radius
    attrs["cx"] = svg.center.x
    attrs["cy"] = svg.center.y
    attrs["fill"] = "none"
    attrs["stroke"] = svg.penColor.toSvgStroke()
    attrs["strokeWidth"] = /*if (script.hover) "4px" else*/ "2px"
  }
}

private fun RBuilder.toSvg(svg: JSvgPathElement) {
  custom("path") {
    attrs["d"] = svg.d
    attrs["fill"] = "none"
    attrs["stroke"] = svg.penColor.toSvgStroke()
    attrs["strokeWidth"] = /*if (script.hover) "4px" else*/ "2px"
  }
}

private fun PenColor.toSvgStroke(): String {
  return when (this) {
    PenColor.BLACK -> "black"
    PenColor.RED -> "red"
    PenColor.WHITE -> "white"
    PenColor.GREEN -> "green"
    PenColor.BLUE -> "blue"
    is PenColor.RGB -> "rgb($r,$g,$b)"
  }
}

private external interface MazeComponentProps : Props {
  var maze: MazePointNet
}

private val MazeComponent = fc<MazeComponentProps> { props ->
  for (wall in props.maze.walls) {
    custom("line") {
      attrs["x1"] = wall.a.x
      attrs["y1"] = wall.a.y
      attrs["x2"] = wall.b.x
      attrs["y2"] = wall.b.y
      attrs["strokeWidth"] = "2px"
      attrs["stroke"] = "gray"
    }
  }
}
